/* Font faces */
@font-face {
  font-family: "nanumfont";
  src: url(./assets/fonts/NanumSquareNeo-eHv.ttf);
}
@font-face {
  font-family: "nanumfont1";
  src: url(./assets/fonts/NanumSquareNeo-cBd.ttf);
}

:root {
  --font-nanumfont: "nanumfont";
  --font-nanumfont1: "nanumfont1";
}

/* Global styles */
body {
  margin: 0;
  font-family: var(--font-nanumfont1);
  overflow-x: hidden; /* Prevent horizontal scrolling */
}

/* Top container */
.top-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  box-sizing: border-box;
  width: 100%;
}

.icon-and-title {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 20px; /* Add margin for spacing */
}

.icon-and-title:hover {
  cursor: pointer;
}

.top-container h1 {
  font-family: var(--font-nanumfont);
  margin-left: 20px; /* Spacing between icon and title */
}

/* Input container */
.icon-and-title {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 20px; /* Add margin for spacing */
}

.input-container {
  display: flex;
  justify-content: center;
  width: 100%;
}

.input {
  display: flex;
  align-items: center;
  gap: 10px;
}

.input input {
  width: 300px;
  height: 48px;
  border: 1px solid black;
  border-radius: 4px;
}

#input2 {
  width: 100px;
  height: 55px;
  border: 1px solid black;
  border-radius: 4px;
}

/* Mid container */
.mid-container {
  display: flex;
  align-items: flex-start;
  padding: 20px;
  box-sizing: border-box;
  height: calc(100vh - 10vh); /* Height minus top and bottom margins */
}

.mid-left-sidebar {
  display: flex;
  flex-direction: column;
  width: 200px; /* Sidebar width */
  font-size: 22px;
  margin-right: 20px; /* Spacing between sidebar and right container */
}

.mid-left-sidebar p {
  cursor: pointer;
  margin: 10px 0; /* Top and bottom margin */
}
.mid-left-sidebar:hover {
  cursor: pointer;
}
.mid-right-container {
  display: flex;
  flex-direction: column; /* Vertical alignment */
  flex-grow: 1; /* Fill remaining space */
  padding: 20px;
  box-sizing: border-box;
  transform: translateX(15%);
}

.mid-carousel {
  width: 100%; /* Carousel width */
  height: 100%; /* Carousel height */
}

.mid-footer {
  margin-top: 20px;
}

/* Footer */
.footer {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px;
  background-color: #f8f9fa; /* 배경색 */
  position: fixed; /* 화면에 고정 */
  bottom: 0; /* 화면 하단에 위치 */
  left: 0; /* 화면 왼쪽에 위치 */
  width: 100%; /* 전체 너비를 차지 */
  box-shadow: 0 -1px 5px rgba(0, 0, 0, 0.1); /* 상단에 그림자 추가 (선택 사항) */
  z-index: 1000; /* 다른 요소들 위에 위치 */
}

.footer a {
  color: #000; /* Icon color */
  font-size: 1.5rem; /* Icon size */
  margin: 0 15px; /* Spacing between icons */
  text-decoration: none; /* Remove underline */
  transition: color 0.3s; /* Optional color transition */
}

.footer a:hover {
  color: #007bff; /* Hover color */
}

.footer p {
  margin: 0; /* Remove default paragraph margin */
}

/* Media queries */
@media (max-width: 1200px) {
  .mid-container {
    flex-direction: column; /* Stack vertically on smaller screens */
  }
}

@media (max-width: 768px) {
  .top-container {
    flex-direction: column; /* Stack vertically on smaller screens */
    align-items: flex-start; /* Top alignment */
  }

  .input-container {
    justify-content: flex-start; /* Left-align input fields */
  }

  .mid-container {
    flex-direction: column; /* Stack vertically on smaller screens */
    align-items: center; /* Center-align */
  }

  .mid-left-sidebar {
    width: 150px; /* Adjust width */
    font-size: 18px; /* Adjust font size */
  }
}

@media (max-width: 576px) {
  .input {
    flex-direction: column; /* Stack input field and button vertically */
    align-items: stretch; /* Stretch input field and button */
  }

  .mid-left-sidebar {
    width: 100px; /* Adjust width for very small screens */
  }
}

/* Additional styles */
.login-form {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.login-form input {
  margin-bottom: 10px;
  padding: 8px;
  width: 200px;
}

.login-form button {
  padding: 10px 20px;
  cursor: pointer;
}

.login-container {
  position: fixed;
  bottom: 10px;
  right: 10px;
  z-index: 1000;
  background-color: white;
  padding: 20px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

.settings-icon {
  position: fixed;
  top: 10px;
  right: 10px;
  cursor: pointer;
  z-index: 1000;
}

.recommendation-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  position: relative;
}

.recommendation-content {
  opacity: 0;
  position: absolute;
  margin-top: 20px;
  width: 100%;
  max-width: 800px;
  margin: 20px auto;
  padding: 20px;
  background: #f9f9f9;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}
.show {
  opacity: 1;
}
.close {
  position: absolute;
  left: 90%;
}
.recommendation-item {
  margin-bottom: 20px;
  background: #f9f9f9;
  padding: 15px;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.recommendation-item h2 {
  margin-top: 0;
}

.recommendation-item p {
  margin: 5px 0;
}

.loading-container {
  display: flex;
  align-items: center;
  justify-content: center;
}

.loader {
  border: 4px solid #f3f3f3;
  border-top: 4px solid #3498db;
  border-radius: 50%;
  width: 12px;
  height: 12px;
  animation: spin 2s linear infinite;
  margin-right: 8px;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.main {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  padding: 20px;
}

.recommendation-container {
  width: 100%;
  max-width: 600px;
  text-align: center;
}
.recommend-btn {
  width: 100px;
}
